import React from 'react';
import Accordion from 'react-bootstrap/Accordion';
import { useAccordionButton } from 'react-bootstrap/AccordionButton';
import styled from 'styled-components';
import { CopyToClipboard } from 'react-copy-to-clipboard';

import { TextSmall, TextMedium, TextLarge } from './CommonStyle';
import { ConfigsType, mobileWidth, mobilePortraitWidth } from '../configs';
import iconCopy from '../assets/images/icon_copy.png';
import '../styles/Account.scss';


type ConfigProps = {
    config: ConfigsType;
};

const Section = styled.section`
    display: grid;
    background: #fbfbfb;
    width: 100%;
    overflow: visible;
    position: relative;
    padding: 3% 15% 3% 15%;
    text-align: center;
    line-height: 150%;
    @media screen and (max-width: ${mobileWidth}px) {
        padding: 5% 10% 5% 10%;
        line-height: 125%;
    }
`;

const Title = styled.div`
    margin: 5% 0 5% 0;
    @media screen and (max-width: ${mobileWidth}px) {
        margin: 5% 0 7% 0;
        line-height: 100%;
    }
`;

const ItemWrapper = styled.div`
    display:inline-block;
    width: 35%;
    margin: 2%;
    vertical-align: top;
    @media screen and (max-width: ${mobilePortraitWidth}px) {
        width: 85%;
    }
`;
const ItemBtn = styled.div`
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: 1fr;
    width: 100%;
    background: #f1eeee;
    margin: 0;
    padding: 3%;
    box-shadow: 0 0 5px lightgray;
`;
const ItemDtl = styled.div`
    width: 100%;
    background: white;
    padding: 3% 0 3% 0;
    text-align:left;
    box-shadow: 0 5px 5px -5px lightgray,
                -5px 0 5px -5px lightgray, 
                5px 0 5px -5px lightgray;
`;
const Item = styled.div`
    padding-left: 5%;
`;

const HR = styled.hr`
    margin: 3%;
`;
const BtnCopy = styled.div`
    text-align: center;
    float: right;
    margin: -3px 3% 0 auto;
    padding: 2px 5px 2px 5px;
    // width: 15%;
    border: solid lightgray;
    border-width: 0.5px;
    border-radius: 10%;
    cursor: pointer;
    @media screen and (max-width: ${mobileWidth}px) {
        margin-top: 0px;
        padding: 0px 5px 0px 5px;
    }
`;
const ImgCopy = styled.img`
    width: 1.5vmin;
    filter: invert(60%);
    margin-right: 2px;
    @media screen and (max-width: ${mobileWidth}px) {
        width: 3vmin;
    }
`;

function CustomToggle(props: { children: any, eventKey: string }) {
	const onSelect = useAccordionButton(props.eventKey, () => console.log(props.eventKey))
  
    return (
      <div className="acnt-toggle" role="button" onClick={onSelect}>
        {props.children}
      </div>
    );
}

const Account = ({ config }: ConfigProps) => {  
    return (
        <Section id="account">
            <Title>
                <TextLarge>  <br/></TextLarge>
            </Title>
            
            <Accordion>
                <ItemWrapper>
                    <CustomToggle eventKey="0">
                        <ItemBtn>
                            <TextMedium> </TextMedium>
                            <TextSmall className="arrow-down">&#12297;</TextSmall>
                        </ItemBtn>
                    </CustomToggle>
                    <Accordion.Collapse eventKey="0">
                        <ItemDtl className="acnt-dtl">
                            <Item>
                                <TextSmall><span className="groomLabel">({config.groom.fatherName})</span> {config.groom.fatherAccountBank}, {config.groom.fatherAccountNumber}</TextSmall>
                                <BtnCopy>
                                    <CopyToClipboard text={config.groom.fatherAccountNumber.replace(/-/g,'')} onCopy={(text: String) => alert("(" +text+ ") .")}>
                                        <span><ImgCopy src={iconCopy} alt="" /><TextSmall></TextSmall></span>
                                    </CopyToClipboard>
                                </BtnCopy>
                            </Item>
                            <HR />
                            <Item>
                                <TextSmall><span className="groomLabel">({config.groom.motherName})</span> {config.groom.motherAccountBank}, {config.groom.motherAccountNumber}</TextSmall>
                                <BtnCopy>
                                    <CopyToClipboard text={config.groom.motherAccountNumber.replace(/-/g,'')} onCopy={(text: String) => alert("(" +text+ ") .")}>
                                        <span><ImgCopy src={iconCopy} alt="" /><TextSmall></TextSmall></span>
                                    </CopyToClipboard>
                                </BtnCopy>
                            </Item>
                            <HR />
                            <Item>
                                <TextSmall><span className="groomLabel">({config.groom.name})</span> {config.groom.accountBank}, {config.groom.accountNumber}</TextSmall>
                                <BtnCopy>
                                    <CopyToClipboard text={config.groom.accountNumber.replace(/-/g,'')} onCopy={(text: String) => alert("(" +text+ ") .")}>
                                        <span><ImgCopy src={iconCopy} alt="" /><TextSmall></TextSmall></span>
                                    </CopyToClipboard>
                                </BtnCopy>
                            </Item>
                        </ItemDtl>
                    </Accordion.Collapse>
                </ItemWrapper>

                <ItemWrapper>
                    <CustomToggle eventKey="1">
                        <ItemBtn>
                            <TextMedium> </TextMedium>
                            <TextSmall className="arrow-down">&#12297;</TextSmall>
                        </ItemBtn>
                    </CustomToggle>
                    <Accordion.Collapse eventKey="1">
                        <ItemDtl className="acnt-dtl py-3">
                            <Item>
                                <TextSmall><span className="brideLabel">({config.bride.fatherName})</span> {config.bride.fatherAccountBank}, {config.bride.fatherAccountNumber}</TextSmall>
                                <BtnCopy>
                                    <CopyToClipboard text={config.bride.fatherAccountNumber.replace(/-/g,'')} onCopy={(text: String) => alert("(" +text+ ") .")}>
                                        <span><ImgCopy src={iconCopy} alt="" /><TextSmall></TextSmall></span>
                                    </CopyToClipboard>
                                </BtnCopy>
                            </Item>
                            <HR />
                            <Item>
                                <TextSmall><span className="brideLabel">({config.bride.motherName})</span> {config.bride.motherAccountBank}, {config.bride.motherAccountNumber}</TextSmall>
                                <BtnCopy>
                                    <CopyToClipboard text={config.bride.motherAccountNumber.replace(/-/g,'')} onCopy={(text: String) => alert("(" +text+ ") .")}>
                                        <span><ImgCopy src={iconCopy} alt="" /><TextSmall></TextSmall></span>
                                    </CopyToClipboard>
                                </BtnCopy>
                            </Item>
                            <HR />
                            <Item>
                                <TextSmall><span className="brideLabel">({config.bride.name})</span> {config.bride.accountBank}, {config.bride.accountNumber}</TextSmall>
                                <BtnCopy>
                                    <CopyToClipboard text={config.bride.accountNumber.replace(/-/g,'')} onCopy={(text: String) => alert("(" +text+ ") .")}>
                                        <span><ImgCopy src={iconCopy} alt="" /><TextSmall></TextSmall></span>
                                    </CopyToClipboard>
                                </BtnCopy>
                            </Item>
                        </ItemDtl>
                    </Accordion.Collapse>
                </ItemWrapper>
            </Accordion>
        </Section>
    );
}

export default Account;